@import "https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap";
:root {
  --blue-color: #00acdc;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  transition: all .3s;
}

body {
  margin: 0;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__logo {
  align-self: center;
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  margin: 0 10px;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__link:hover {
  color: var(--blue-color);
}

.nav__item:first-child .nav__link {
  margin-left: 0;
}

.nav__item:last-child .nav__link {
  margin-right: 0;
}

.is-active {
  color: var(--blue-color);
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
